// TITLE :: START

.c-title {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column-reverse;

  &--features {
    padding-bottom: 60px;
  }
  &--request {
    padding-bottom: 50px;

    h1 {
      color: $c-white;
    }
  }
  &--cloud {
    padding-bottom: 20px;
  }
  &--analytics {
    padding-bottom: 20px;
  }
  &--papelines   {
    padding-bottom: 20px;
  }
  &--ai {
    padding-bottom: 42px;
  }

  &__text {
    text-align: center;
    line-height: 1.2;
    font-weight: 400;
    font-family: "Muli", sans-serif;
    @include fluid(font-size, 320px, 1600px, 25px, 40px);

    &-wrap {}
  }

  &__icon {
    &-wrap {
      padding-bottom: 22px;

      svg {
        fill: #cd084b;
      }

      .icon-request {
        fill: rgba($c-white, 0.4);
      }
    }
  }
}

// TITLE :: END