// BUTTON :: START

.c-btn {
  outline: none;
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  max-width: 291px;
  height: 70px;
  border-radius: 10px;
  border: 2px solid #cd084b;
  background-color: transparent;
  color: #cd084b;
  text-transform: uppercase;
  letter-spacing: 1.35px;
  font-size: 18px;
  font-weight: bold;
  @include transition(background-color, color);

  &--features {
    margin-left: auto;
    margin-right: auto;
  }

  &--border {
    &:hover {
      background-color: #cd084b;
      color: $c-white;
      @include transition(background-color, color);
    }
  }
}

// BUTTON :: END